<script>
export let treeData = [];
export let rootName = 'root';
export let indentLevel = 0;

let isExpanded;
function generateEmptyEntry () {
	return {
		children: [],
		id: -1,
		name: `${rootName}: No properties found`,
	};
}


$: isExpanded = treeData.length === 1;
</script>

{#if treeData.length > 0}
	{#each treeData as leaf (leaf.id)}
		<details style={`margin-left: ${indentLevel / 2}em`} open={isExpanded}>
			<summary aria-disabled={leaf.children.length === 0}>{leaf.name}</summary>
			{#if leaf.children.length > 0}
				<svelte:self
					treeData={leaf.children}
					indentLevel={indentLevel + 1}
				/>
			{/if}
		</details>
	{/each}
{:else}
	<svelte:self treeData={[generateEmptyEntry()]}/>
{/if}

<style>
details {
	font-family: Arial, Helvetica, sans-serif;
}

summary {
	margin: 0.5em 0;
	padding: 0.25em;
}

summary[aria-disabled='true']::-webkit-details-marker {
	display: none;
}

summary[aria-disabled='false']:hover {
	background: #ddd;
	cursor: pointer;
}

summary[aria-disabled='true'] {
	list-style: none;
}

summary[aria-disabled='true']::before {
	content: "";
	line-height: 1em;
	width: 0.5em;
	height: 0.5em;
	background-color: #ddd;
	border-radius: 50%;
	float: left;
	padding: 0.05em;
	margin-top: 0.25em;
	margin-right: 0.5em;
}
</style>
