<script>
import { bfServer, activeDataType } from './stores';
import { worker as bfmtDataWorker } from './bfmtData';
import DetailSummaryTree from './DetailSummaryTree.svelte';
export let key;
let dataPromise;

const FALSY_TYPES_TO_DISPLAY = ['number', 'string', 'boolean'];

function generateEmptyEntry () {
	return {
		children: [],
		id: -1,
		name: `${key}: No properties found`,
	};
}

function generateTreeNodeForEntry (prop, obj, currentPath = '') {
	let name, children = [];
	const value = obj[prop];
	const valueType = typeof value;
	const nextPath = [currentPath, prop].filter(v => v).join('.');
	if (!value || valueType !== 'object') {
		const valueTypeToDisplay = value || FALSY_TYPES_TO_DISPLAY.includes(valueType) ? `: ${valueType}` : '';
		let valueToDisplay = value;
		if (!value && valueType === 'string') {
			valueToDisplay = '(Empty String)';
		}
		name = `${prop}${valueTypeToDisplay} = ${valueToDisplay}`;
	} else if (Array.isArray(value)) {
		const arrayType = value.reduce((currentType, val) => (!currentType || currentType === typeof val) ? typeof val : 'any', '');
		name = `${prop}: ${arrayType}[${value.length}]`;
		children = value.map((subItem, i) => generateTreeNodeForEntry(i, value, nextPath));
	} else {
		name = `${prop}: object{${Object.keys(value).length}}`;
		children = Object.keys(value).map((k) => generateTreeNodeForEntry(k, value, nextPath));
	}

	return {
		children,
		id: nextPath,
		name,
	};
}

function createTree (obj) {
	const keys = Object.keys(obj);
	let result;
	if (keys.length === 0) {
		result = generateEmptyEntry();
	} else {
		result = keys.map((k) => generateTreeNodeForEntry(k, obj));
	}
	return result;
}

$: dataPromise = (key ? bfmtDataWorker.getEntry($bfServer, $activeDataType, key) : Promise.resolve({}))
	.then(createTree);
</script>

<article>
	{#await dataPromise}
		Loading entry {key}...
	{:then data}
		<DetailSummaryTree treeData={data}/>
	{:catch error}
		Error getting entry {key}.<br>>{console.error(error) || error}
	{/await}
</article>
