export const SERVERS = ['eu', 'gl', 'jp', 'kr'];
export const SERVER_NAME_MAPPING = {
	eu: 'Europe (EU)',
	gl: 'Global (GL)',
	jp: 'Japan (JP)',
	kr: 'Korea (KR)',
};
export const DATA_TYPES_BY_SERVER = {
	default: ['item', 'unit'],
	gl: ['burst', 'dictionary', 'extra-skill', 'item', 'leader-skill', 'mission', 'unit'],
};

/**
 * @description List of available properties in addition to their respective key and the `bfmtMetadata` property
 */
export const SEARCHABLE_PROPERTIES_BY_DATA_TYPE = {
	burst: ['desc', 'name'],
	dictionary: [],
	'extra-skill': ['desc', 'name'],
	item: ['desc', 'name', 'thumbnail'],
	'leader-skill': ['desc', 'name'],
	mission: ['desc', 'name'],
	unit: ['name'],
};

/**
 * @type {{ [key: string]: function ([string, object], [string, object]): number }}
 */
export const SORT_TYPES = {
	ID: ({ key: keyA }, { key: keyB }) => `${keyA}`.localeCompare(`${keyB}`),
	'Update Date': ({ key: keyA, value: entryA }, { key: keyB, value: entryB }) => {
		let diff = new Date(entryA.bfmtMetadata.updatedAt) - new Date(entryB.bfmtMetadata.updatedAt);
		if (diff === 0) {
			diff = SORT_TYPES.ID([keyA], [keyB]);
		}
		return diff;
	},
};
