<script>
import '../node_modules/dialog-polyfill/dist/dialog-polyfill.css';
import { onMount, tick } from 'svelte';
import { bfServer, activeDataType, sortParameters } from './stores';
import {
	SERVERS,
	DATA_TYPES_BY_SERVER,
	SERVER_NAME_MAPPING,
	SORT_TYPES,
} from './constants';
import { worker as bfmtDataWorker } from './bfmtData';
import formatDistanceToNow from 'date-fns/formatDistanceToNow';
import dialogPolyfill from 'dialog-polyfill';
import EntryViewer from './EntryViewer.svelte';
const allSortTypes = Object.keys(SORT_TYPES);
const pageSize = 100;

let lastKnownServer;
let lastQueriedServer;
let lastQueriedDataType;
let lastTextQuery;
let navigationContextChanged = false;
let localActiveDataType = '';
let possibleActiveDataTypes = [];
let localSortType;
let localSortMultiplier;
let textQuery = '';
let searchResultPromise = Promise.resolve([]);
let searchKey;
let searchIndex = 0;
let activeKey;
let storedScrollPosition;
let totalEntries;
let entryDialog;
let modalCloseButton;
let lastFocusedElement;
let atTop = true;
let scrollY = 0;
let isLoading = true;
let isFirstLoad = true;
const activeDataTypeUnsubscribe = activeDataType.subscribe((dataType) => {
	localActiveDataType = dataType || '';
});

const bfServerUnsubscribe = bfServer.subscribe((server) => {
	if (isFirstLoad) {
		lastKnownServer = server;
		isFirstLoad = false;
	}
	possibleActiveDataTypes = (DATA_TYPES_BY_SERVER[server] || DATA_TYPES_BY_SERVER.default).slice();
	// update active data type on server change
	if (!localActiveDataType || lastKnownServer !== server) {
		activeDataType.set(possibleActiveDataTypes[0]);
	}
	lastKnownServer = server;
});

$: navigationContextChanged = lastKnownServer !== lastQueriedServer || localActiveDataType !== lastQueriedDataType;
$: atTop = scrollY === 0;

// saving and setting focus state based on whether the dialog is open
$: if (activeKey) {
	lastFocusedElement = document.activeElement;
	if (!entryDialog.showModal) {
		dialogPolyfill.registerDialog(entryDialog);
	}
	entryDialog.showModal();
	tick().then(() => {
		modalCloseButton.focus();
	});
}

$: if (!activeKey && lastFocusedElement) {
	tick().then(() => {
		lastFocusedElement.focus();
		lastFocusedElement = null;
	});
}

const sortParameterUnsubscribe = sortParameters.subscribe((params = '') => {
	[localSortType, localSortMultiplier] = params.split(',');
});

function search() {
	const searchContext = {
		searchKey,
		server: lastQueriedServer,
		dataType: lastQueriedDataType,
		textQuery,
		sortType: localSortType,
		sortMultiplier: +localSortMultiplier,
		pageSize,
	};

	isLoading = true;
	if (searchContext.textQuery !== lastTextQuery) {
		searchResultPromise = bfmtDataWorker.getEntriesForSearch(
			searchContext.server,
			searchContext.dataType,
			{
				filters: {
					text: searchContext.textQuery,
				},
				sortType: searchContext.sortType,
				sortMultiplier: searchContext.sortMultiplier,
				maxResultSize: searchContext.pageSize,
			},
		).then((result) => {
			lastTextQuery = searchContext.textQuery;
			return result;
		});
	} else {
		searchResultPromise = bfmtDataWorker.updateSearchContextSort(
			searchContext.searchKey,
			{
				sortType: searchContext.sortType,
				sortMultiplier: searchContext.sortMultiplier,
				maxResultSize: searchContext.pageSize,
			},
		);
	}

	searchResultPromise = searchResultPromise.then((result) => {
		bfServer.set(searchContext.server);
		activeDataType.set(searchContext.dataType);
		sortParameters.set(`${searchContext.sortType},${searchContext.sortMultiplier}`);
		searchKey = result.searchKey;
		searchIndex = 0;
		totalEntries = result.totalEntries;
		return result.results;
	}).finally(() => {
		isLoading = false;
	});
}

function setPageDataContext () {
	isLoading = true;
	searchResultPromise = bfmtDataWorker.getUpdateStatsForServerAndDataType(
		lastKnownServer,
		localActiveDataType,
		{
			sortType: localSortType,
			sortMultiplier: +localSortMultiplier,
			maxResultSize: pageSize,
		},
	).then((result) => {
		lastQueriedServer = lastKnownServer;
		lastQueriedDataType = localActiveDataType;
		textQuery = '';
		lastTextQuery = '';
		searchKey = result.searchKey;
		searchIndex = 0;
		totalEntries = result.totalEntries;
		return result.results;
	}).finally(() => {
		isLoading = false;
	});
}

function getEntryName ({ key = '', value = {} } = {}) {
	return value.name ? `${value.name} (${key})` : key;
}

function getEntryDate ({ value = { bfmtMetadata: { updatedAt: '' } }} = {}) {
	return formatDistanceToNow(new Date(value.bfmtMetadata.updatedAt), { addSuffix: true });
}

function getEntriesForPage (index) {
	isLoading = true;
	searchResultPromise = bfmtDataWorker.getEntriesInSearchContext(
		searchKey,
		Math.max(Math.min(index, totalEntries), 0),
		pageSize,
	).then((result) => {
		searchIndex = index;
		return result;
	}).finally(() => {
		isLoading = false;
	});
}

onMount(() => {
	window.bfmtData = bfmtDataWorker;
	setPageDataContext();
});

$: if (activeKey) {
	storedScrollPosition = window.scrollY;
} else {
	window.scrollTo(0, storedScrollPosition);
}
</script>

<svelte:window bind:scrollY={scrollY}/>

<dialog bind:this={entryDialog} on:close={() =>{ activeKey = '' }}>
	<header>
		<form method="dialog">
			<button bind:this={modalCloseButton} value="cancel">Close</button>
		</form>
	</header>
	<EntryViewer key={activeKey}/>
</dialog>
<main>

	<header>
		<nav id="page-header">
			<form on:submit|preventDefault={setPageDataContext}>
				<span>Showing </span> <select
					name="data-type"
					id="data-type"
					bind:value={$activeDataType}
					disabled={isLoading}
				>
					{#each possibleActiveDataTypes as dataType}
						<option value={dataType}>{dataType}</option>
					{/each}
				</select> <span> entries from the </span> <select
					name="server"
					id="server"
					bind:value={$bfServer}
					disabled={isLoading}
				>
					{#each SERVERS as server}
						<option value={server}>{SERVER_NAME_MAPPING[server] || server}</option>
					{/each}
				</select> <span> server.</span>

				{#if navigationContextChanged}
					<button type="submit" disabled={isLoading}>Update</button>
				{/if}
			</form>

			<a href="https://github.com/BluuArc/bfmt-data" id="github-link">GitHub</a>
		</nav>
	</header>

	<form on:submit|preventDefault={search} id="search-form">
		<fieldset>
			<legend>Search and Sort</legend>
			<ul>
				<li>
					<label for="text-query">Text Filter:</label>
					<input id="text-query" name="text-query" bind:value={textQuery} disabled={isLoading}>
				</li>
				<li>
					<span>Sort by </span> <select
						name="sort-type"
						id="sort-type"
						bind:value={localSortType}
						disabled={isLoading}
					>
						{#each allSortTypes as sortType}
							<option value={sortType}>{sortType}</option>
						{/each}
					</select> <span> in </span> <select
						name="sort-order"
						id="sort-order"
						bind:value={localSortMultiplier}
						disabled={isLoading}
					>
						<option value="1">ascending</option>
						<option value="-1">descending</option>
					</select> <span> order.</span>
				</li>
			</ul>
			<button type="submit" disabled={isLoading}>Search</button>
		</fieldset>
	</form>

	<section>
		{#await searchResultPromise}
			Loading entries...
		{:then data}
			<ol id="result-list">
				{#each data as entry, i (entry.key)}
					<li>
						<span class="index-cell">{searchIndex + i + 1}</span>
						<button class="name-cell" on:click={() => { activeKey = entry.key; }}>
							{getEntryName(entry)}
						</button>
						<span class="date-cell">{getEntryDate(entry)}</span>
					</li>
				{/each}
			</ol>
		{:catch error}
			Error loading data.<br>{console.error(error) || error}
		{/await}
		<footer class:sticky={!activeKey}>
			<nav>
				<button disabled={searchIndex === 0} on:click={() => getEntriesForPage(searchIndex - pageSize)}>Previous {searchIndex !== 0 ? pageSize : '' }</button>
				<button disabled={atTop} on:click={() => { window.scrollTo(0, 0); } }>To Top</button>
				<button disabled={(searchIndex + pageSize) > totalEntries} on:click={() => getEntriesForPage(searchIndex + pageSize)}>Next {searchIndex + 2 * pageSize <= totalEntries ? pageSize : (totalEntries % pageSize)} </button>
			</nav>
		</footer>
	</section>
</main>

<style>
main {
	font-family: Arial, Helvetica, sans-serif;
}

form ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

form li + li {
	margin-top: 1em;
}

#search-form {
	padding: 1em;
}

#search-form button[type='submit'] {
	margin-top: 1em;
}

nav#page-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

nav#page-header form {
	margin-left: 1em;
}

nav#page-header a#github-link {
	flex: none;
}

dialog {
	max-width: 100vw;
}

dialog header {
	position: sticky;
	top: 0;
}

#result-list {
	padding: 0;
	margin: 0;
	list-style: none;
	padding: 0;
}

#result-list li {
	display: flex;
	padding: 0.5em 1em;
	min-height: max(48px, 1em);
	align-items: center;
}

#result-list li:nth-child(odd) {
	background-color: #eee;
}

#result-list li * {
	margin: 0 0.25em;
}

#result-list li .index-cell, #result-list li .date-cell {
	flex: none;
}

#result-list li .name-cell {
	flex: 1;
	border: 0;
	background: none;
	text-align: left;
	text-decoration: underline;
	align-self: stretch;
}

#result-list li .name-cell:hover {
	background: #ddd;
	cursor: pointer;
}

footer.sticky {
	position: sticky;
	bottom: 0;
}

footer nav {
	display: flex;
	justify-content: space-between;
	margin-top: 0.5em;
	padding: 0.5em;
	background: white;
	height: 1.5em;
}

footer nav button {
	flex: none;
}
</style>
