import { writable } from 'svelte/store';
import { SERVERS, SORT_TYPES } from './constants';

const allSortTypes = Object.keys(SORT_TYPES);

function createLocalStorageStore (key, initialValue, isValid = () => true) {
	if (!isValid(initialValue)) {
		throw new Error(`Initial value is not valid for key:${key}`);
	}
	const startingValue = localStorage.getItem(key) || initialValue;

	const store = writable(startingValue);
	store.subscribe((v) => {
		if (isValid(v) || v === initialValue) {
			localStorage.setItem(key, v);
		} else {
			store.set(initialValue);
		}
	});

	return store;
}

export const bfServer = createLocalStorageStore('bfmt-data:server', 'gl', (v) => SERVERS.includes(v));
export const activeDataType = createLocalStorageStore('bfmt-data:activeDataType');

/**
 * @description parameters are defined as `sortType,sortMultiplier`
 */
export const sortParameters = createLocalStorageStore(`bfmt-data:sortParmeters`, 'ID,1', (v = '') => {
	const [sortType, sortMultiplier] = v.split(',');
	return !!v && sortType && sortMultiplier && allSortTypes.includes(sortType) && !isNaN(sortMultiplier);
});
